



































































































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import {HttpService} from "@/services/ApiService";
import {Dboy, Vehicle, Wallet} from "@/models/service";


@Component
export default class MensajerosDetail extends Vue {
  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';

  public repartidor: Dboy| null = null;

  public vehicles: Vehicle[] = [];
  public wallets: Wallet[] = [];
  public errors = {};

  public isLoading = true

  public imageTitle = '';
  public imageName = '';
  public imageSrc = '';
  public eventImageFile: null | File = null;



  async changeVerifiedVehicle(id: number) {
    await HttpService.get(`vehiculos/${id}/cambiar/verificar` ,
        this.setVehicle)
  }

  async deleteVehicle(id: number) {
    await HttpService.delete(`vehiculos/${id}/eliminar` ,
        this.deleteVehicleItem)
  }
  uploadImage(event: any){
    this.eventImageFile = event.target.files[0]
  }

  async getRepartidor() {
    await HttpService.get(`mensajeros/${this.$route.params.id}/resumen` ,
        this.setRepartidor)
  }

  async updateRepartidor() {
    this.errors = {}
    try{
      await HttpService.put(`mensajeros/${this.$route.params.id}/update` , this.repartidor ?? {},
          this.setRepartidor)
    }catch (e){
     if(e.response.status == 422){
       this.errors = e.response.data.errors
     }
    }
  }

  async updateImageDboy() {
    let data = new FormData;

    data.append('name', this.imageName);
    data.append('file', this.eventImageFile!);

    //let data = {'name': this.imageName, 'file': this.eventImageFile}

    // @ts-ignore
    this.$refs.modalDelivery.close()

    await HttpService.post(`mensajeros/${this.$route.params.id}/foto` ,
        data,
        this.setRepartidor)
   this.eventImageFile = null;
  }


  openDialog(title: string, name: string, src: string){
    this.imageTitle = title;
    this.imageName = name;
    this.imageSrc = src;
    // @ts-ignore
    this.$refs.modalDelivery.open()
  }


  setRepartidor(data: any){
    console.log(data)
    this.repartidor = data.data;
    if( data.vehicles != undefined){
      this.vehicles = data.vehicles;
    }

    if( data.wallets != undefined){
      this.wallets = data.wallets;
    }
  }


  setVehicle(data:any){
    let vehicle: Vehicle = data.data;
    let index = this.vehicles.findIndex(e => e.id == vehicle.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      this.$set(this.vehicles, index, vehicle)
    }
  }

  deleteVehicleItem(data:any){
    let vehicle: Vehicle = data.data;
    let index = this.vehicles.findIndex(e => e.id == vehicle.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      this.$delete(this.vehicles, index)
    }
  }

  async created(){
    await this.getRepartidor();
    this.isLoading =false;


  }

}
